.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: $widht-card;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  flex-grow: 0;
  
  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
    object-fit: fill;
  }

  &__name {
    display: block;
    color: $text-main-accent;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-bottom: 4px;
  }

  
  &__code {
    text-align: left;
    color: $text-secondary;
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    margin-bottom: 16px;
  }

  &__preview {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    margin-bottom: 24px;

    &-opinion {
      color: $text-main-accent;
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
    }
  }

  &__pay {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    margin-bottom: 16px;

    &-name {
      color: $text-secondary;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
    }

    &-price {
      color: $text-main-accent;
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      text-align: right;
    }
  }
}

.pay {
  display: flex;
  box-sizing: border-box;
  height: 40px;
  width: 100%;
  background: $color-main;
  border: $color-main;
  border-radius: 5px;
  color: #fff;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
}

.pay:hover {
  color: $color-main;
  background: $background-color;
  border: 1px solid $color-main;
}
